:root {
  --opehuone--container--width: 1410px;
  --opehuone--container--padding: 1rem;
  --opehuone--spacing--2-x-small: 0.875rem;
  --opehuone--spacing--x-small: 1rem;
  --opehuone--spacing--small: clamp(1.5rem, 2vw, 1.875rem);
  --opehuone--spacing--normal: clamp(1.8rem, 3vw, 2.5rem);
  --opehuone--spacing--medium: clamp(
  	2.8rem,
  	4vw,
  	3.125rem
  );
  --opehuone--spacing--large: clamp(3rem, 5vw, 3.75rem);
  --opehuone--spacing--2-x-large: clamp(5rem, 7vw, 7.5rem);
  --opehuone--font-size--x-small: 0.75rem;
  --opehuone--font-size--small: 0.875rem;
  --opehuone--font-size--normal: 1rem;
  --opehuone--font-size--medium: clamp(1rem, 2vw, 1.125rem);
  --opehuone--font-size--2-x-medium: clamp(1.125rem, 2vw, 1.25rem);
  --opehuone--font-size--large: clamp(
  	1.125rem,
  	2vw,
  	1.5rem
  );
  --opehuone--font-size--x-large: clamp(1.5rem, 3vw, 2rem);
  --opehuone--font-size--2-x-large: clamp(2rem, 3.5vw, 2.5rem);
  --opehuone--font-size--3-x-large: clamp(
  	2.5rem,
  	4vw,
  	3rem
  );
}

button {
  cursor: pointer;
}

.opehuone-content-container {
  max-width: var(--opehuone--container--width);
  width: 100%;
  padding-left: var(--opehuone--container--padding);
  padding-right: var(--opehuone--container--padding);
  margin-left: auto;
  margin-right: auto;
}

.hds-container {
  max-width: var(--opehuone--container--width);
  width: 100%;
  padding-left: var(--opehuone--container--padding);
  padding-right: var(--opehuone--container--padding);
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .hds-container {
    padding-left: var(--opehuone--container--padding);
    padding-right: var(--opehuone--container--padding);
  }
}
@media (min-width: 1248px) {
  .hds-container {
    max-width: var(--opehuone--container--width);
    padding-left: var(--opehuone--container--padding);
    padding-right: var(--opehuone--container--padding);
  }
}

.hds-container--wide {
  max-width: 1920px;
}

.date-box-row {
  display: grid;
  align-items: center;
  grid-template-columns: auto 90px;
  gap: 0.75rem;
}

.date-box-row-weather {
  font-size: var(--opehuone--font-size--medium);
  font-weight: 700;
  text-align: center;
}

.date-box-week {
  display: block;
  font-size: var(--opehuone--font-size--medium);
}

.date-box-month {
  display: block;
  font-size: var(--opehuone--font-size--medium);
  font-weight: 700;
}

.date-box-info {
  margin-top: var(--opehuone--spacing--small);
  display: block;
  font-weight: 500;
  font-size: var(--opehuone--font-size--small);
}

.opehuone-form-field-group {
  margin-bottom: 1rem;
}

.opehuone-form-label {
  font-weight: bold;
  font-size: 0.875rem;
  line-height: 1.1428571429;
  margin-bottom: 0.5rem;
}

.opehuone-form-field-description {
  font-size: 0.875rem;
}

.opehuone-form-label-sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.opehuone-form-mandatory-indication {
  margin-left: 0.5rem;
  font-size: 1.125rem;
  font-weight: 700;
  color: #000;
}

.opehuone-form-control {
  display: block;
  width: 100%;
  height: auto;
  padding: 1rem 1.25rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.1875;
  color: #000;
  background-color: #fff;
  background-clip: padding-box;
  border: 0.0625rem solid #000;
  border-radius: 0;
  box-sizing: border-box;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
.opehuone-form-control:focus {
  outline: 0;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.75);
}
.opehuone-form-control::placeholder {
  color: #000;
  opacity: 1;
}

/**
 * Gravity forms
 * Comment out if needed
 */
.gsection_title {
  color: var(--wp--preset--color--accent-2);
}

.add-new-service-form__wrapper {
  display: none;
  margin-bottom: 1.25rem;
}
.add-new-service-form__title {
  font-size: 1.5625rem;
  margin-top: 0;
  margin-bottom: 0.9375rem;
}
.add-new-service-form__subtitle {
