  font-size: 1rem;
  margin-top: 0;
  margin-bottom: 1.25rem;
}
.add-new-service-form__field-group {
  margin-bottom: 1.25rem;
}
.add-new-service-form__label {
  display: block;
  font-size: 1rem;
  line-height: 1.375;
  color: #000;
  font-weight: bold;
}
