}
.has-theme-dark .add-new-service-form__btn.add-new-service-form__btn--submit {
  color: #000;
  background-color: #FFF;
}
.add-new-service-form__notifications {
  display: none;
  margin-top: 1.25rem;
  border: 0.125rem solid #000;
  padding: 0.9375rem;
