}
.side-links-list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  gap: 0.625rem;
}
.side-links-list__link {
  color: #000;
  font-size: var(--opehuone--font-size--normal);
  font-weight: 700;
  text-decoration: none;
}
.side-links-list__link:hover, .side-links-list__link:focus {
  color: #000;
  text-decoration: underline;
}
