}
.side-links-list__form-wrapper {
  display: none;
}
.side-links-list-box--modification-ongoing .side-links-list__form-wrapper {
  display: block;
  margin-top: var(--opehuone--spacing--normal);
}
.side-links-list__reset-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  color: #fff;
  background-color: red;
  text-align: center;
  text-decoration: none;
  white-space: normal;
  vertical-align: middle;
  user-select: none;
  border: 2px solid red;
  padding: 0.625rem 2rem;
  font-size: var(--opehuone--font-size--normal);
  border-radius: 0;
