}
.own-links__submit-btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.opehuone-grid {
  display: grid;
  grid-template-columns: auto;
  gap: var(--opehuone--spacing--normal);
  width: 100%;
}
@media (min-width: 768px) {
  .opehuone-grid.opehuone-grid--reversed {
    grid-template-columns: 360px 1fr;
  }
}
@media (min-width: 768px) {
  .opehuone-grid {
    grid-template-columns: 1fr 360px;
  }
}

:where(.wp-site-blocks) > footer.wp-block-template-part {
  margin-block-start: 0;
}

.sidebar-boxes {
  display: flex;
  flex-direction: column;
  gap: var(--opehuone--spacing--normal);
}

.sidebar-box {
  padding: 1.6875rem 1.25rem;
}
.sidebar-box.sidebar-box--suomenlinna-light {
  background-color: var(--color-suomenlinna-light);
}
.sidebar-box.sidebar-box--coat-of-arms-light {
  background-color: var(--color-coat-of-arms-light);
}
