}
.sidebar-box__sub-title {
  margin-top: 0;
  margin-bottom: var(--opehuone--spacing--small);
  font-size: var(--opehuone--font-size--large);
  font-weight: 500;
}
.sidebar-box__sub-title.sidebar-box__sub-title--margin-top {
  margin-top: var(--opehuone--spacing--small);
}

.service-failure {
  position: relative;
  padding: 1rem 1.5rem;
  background-color: var(--color-info-light);
  margin-bottom: var(--opehuone--spacing--normal);
}
.service-failure:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 8px;
  background-color: var(--color-info);
}
.service-failure.service-failure--error {
  background-color: var(--color-error-light);
}
.service-failure.service-failure--error:before {
  background-color: var(--color-error);
}
.service-failure__title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  margin-top: 0;
  margin-bottom: var(--opehuone--font-size--normal);
  font-weight: 700;
  font-size: var(--opehuone--font-size--medium);
}
.service-failure p {
  font-size: var(--opehuone--font-size--small);
  font-weight: 500;
}
.service-failure__toggler {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  gap: 15px;
  background-color: transparent;
  border: none;
  font-size: var(--opehuone--font-size--small);
  font-weight: 500;
  padding: 0;
}
.service-failure__toggler:hover, .service-failure__toggler:focus {
  text-decoration: underline;
}
.service-failure--read-more-open .service-failure__toggler > svg {
  transform: rotate(180deg);
}
.service-failure__read-more-section {
  display: none;
  margin-top: var(--opehuone--spacing--small);
}
.service-failure--read-more-open .service-failure__read-more-section {
  display: block;
}

.user-favs-list {
  list-style-type: none;
  margin: 0 0 var(--opehuone--spacing--medium) 0;
  padding: 0;
  display: grid;
  grid-template-columns: auto;
  gap: 1.375rem;
}
.user-favs-list.user-favs-list--grid {
